import { ListGroup, ListGroupItem } from 'react-bootstrap';
import { useSelector } from 'react-redux';
import Question, { QuestionState } from '../model/Question';

export default function QuestionHistory() {
  const history = useSelector((state: any) => state.question.history)

  function getStyle(question: Question) {
    return (question.state == QuestionState.RIGHT)? "success" : "danger";
  }

  return (
    <>
      <h3 className='any-heading'>Question History</h3>
      <ListGroup style={{maxWidth: '18rem'}} className="question-history">
        {history.map((question: any) => <ListGroupItem key="" variant={getStyle(question)}>
          {question.left} x {question.right} = {question.left * question.right}
          </ListGroupItem>)}
      </ListGroup>
    </>
  );
}
