import Card from 'react-bootstrap/Card';
import { useSelector } from 'react-redux';

export default function YourScore() {
  const score = useSelector((state: any) => state.question.score)

  return (
    <Card style={{ width: '18rem' }} className="your-score">
      <Card.Body>
        <Card.Title>Your Score</Card.Title>
        <Card.Text>
          {score.right} / {score.right + score.wrong}
        </Card.Text>
      </Card.Body>
    </Card>
  );
}
