import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import { KeyboardEvent } from 'react';
import { useRef, RefObject } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { trySolve, nextQuestion } from '../features/questions/QuestionsSlice';
import Question, { QuestionState, SubmissionType } from '../model/Question';
import { getRandomInt } from '../util/random';

type SubmissionCallback = ((e: HTMLInputElement | HTMLButtonElement | null) => void);

interface QuestionProps { submissionType: SubmissionType }
interface QuestionCardDecorations {feedbackClass: string, result: string, submitEnabled: boolean}
interface HintProps { value: number, callback: (e: HTMLButtonElement | null) => void }
interface SubmissionProps { question: Question, submissionType: SubmissionType, callback: SubmissionCallback}
interface TextFieldSubmissionProps { callback: (e: HTMLInputElement | null) => void}
interface ButtonsSubmissionProps { values: number[], callback: (e: HTMLButtonElement | null) => void}

function getHints(question: Question) {
  let actual = question.left * question.right
  if (question.submitted != undefined) {
    return [question.submitted]
  }
  let floor = Math.max(actual - getRandomInt(3), 1)
  return [floor, floor + 1, floor + 2]
}

function Hint(props: HintProps) {
  const subBtn = useRef<HTMLButtonElement>(null)
    return (
      <Button className="hint-button" variant="undefined" value={props.value} style={{marginLeft: "5px"}} key={props.value} ref={subBtn} onClick={() => props.callback(subBtn.current)}>{props.value}</Button>
    )
}

function Submission(props: SubmissionProps) {

  if (props.submissionType === SubmissionType.SELECT) {
    return (
      <ButtonsSubmission values={getHints(props.question)} callback={props.callback}/>
    )
  }

  return (
    <TextFieldSubmission callback={props.callback}/>
  )
}

function TextFieldSubmission(props: TextFieldSubmissionProps) {
  const subInput = useRef<HTMLInputElement>(null)

  function handleKeyDown(event: KeyboardEvent<HTMLInputElement>) {
    if (event.key === 'Enter') {
      props.callback(subInput.current);
    }
  }

  return (
    <div>
      <input type="number" ref={subInput} onKeyDown={handleKeyDown}/>
    </div>
  )
}

function ButtonsSubmission(props: ButtonsSubmissionProps) {

  return (
    <div>
      {props.values.map((value) => <Hint key={value} value={value} callback={props.callback} />)}
    </div>
  )
}

function _getCardDecoration(question: Question) {
  switch (question.state) {
    case QuestionState.RIGHT:
      return {cardClasses: "question-card question-card-right", resultHint: String(question.left * question.right), enabled: false}
    case QuestionState.WRONG:
      return {cardClasses: "question-card question-card-wrong", resultHint: String(question.left * question.right), enabled: false}
    case QuestionState.PENDING:
      return {cardClasses: "question-card question-card-pending", resultHint: "?", enabled: true}
  }
}

export default function QuestionCard(props: QuestionProps) {
  const score = useSelector((state: any) => state.question.score)
  const question = useSelector((state: any) => state.question.current)
  const dispatch = useDispatch()

  function trySolveAndSwitch(submission: HTMLInputElement | HTMLButtonElement | null) {
    if (submission && submission.value) {
      dispatch(trySolve(parseInt(submission.value)));
      setTimeout(() => dispatch(nextQuestion()), 1500);
    }
  }

  const decorations = _getCardDecoration(question)
  const questionNumber = score.right + score.wrong + (decorations.enabled? 1 : 0)

  return (
    <div className="question" style={{width: '18rem'}}>
      <h3 className='any-heading'>Question #{questionNumber}</h3>
      <Card className={decorations.cardClasses}>
        <Card.Body>
          <Card.Text>
          {question.left} x {question.right} = {decorations.resultHint} <br/> Enter the answer below:
          </Card.Text>
          <fieldset disabled={!decorations.enabled}>
            <Submission question={question} submissionType={props.submissionType} callback={trySolveAndSwitch}/>
          </fieldset>
        </Card.Body>
      </Card>
    </div>
  );
}

