import { useSelector } from 'react-redux';
import { Container, ListGroup, ListGroupItem, Row, Col } from 'react-bootstrap';
import HighScore from '../model/HighScore';


export default function HighScores() {
  const highScores = useSelector((state: any) => state.highScores)

  return (
    <div className="scores-section">
      <h5 className='any-heading'>High Scores</h5>
      <ListGroup className="high-score-table">
        {[...highScores.keys()].map((idx: number) => <ListGroupItem variant="info" className="high-score-row">
            <Container fluid>
              <Row>
                <Col>{highScores[idx].who}</Col><Col>{highScores[idx].score}</Col></Row>
            </Container>
          </ListGroupItem>)}
      </ListGroup>
    </div>
  );
}

