import { createSlice } from '@reduxjs/toolkit'
import HighScore from '../../model/HighScore';
import { getRandomInt } from '../../util/random';

const upperLimit = 50

function toScore(_: number): HighScore {
    return {score: getRandomInt(upperLimit), who: "AAA"}
}

function getSomeRandomScores(): HighScore[] {
    return [1,1,1,1,1,1,1,1,1].map(toScore).sort((a, b) => {return b.score - a.score})
}

export const highScores = createSlice({
  name: 'highScores',
  initialState: getSomeRandomScores(),
  reducers: {
    updateScores: (state: any) => {
        console.log("Reduce me!")
        return state
    }
  },
})

export const {updateScores } = highScores.actions

export default highScores.reducer