import Question, { QuestionState }  from '../../model/Question'
import { createSlice } from '@reduxjs/toolkit'
import { getRandomInt } from '../../util/random';
import type { PayloadAction } from '@reduxjs/toolkit';


function newQuestion() {
    return {left: 1 + getRandomInt(12), right: 1 + getRandomInt(12), state: QuestionState.PENDING}
}

let initialState: {score: {right: number, wrong: number}, current: Question | null, history: Question[]} = {
    score: {right: 0, wrong: 0},
    current: newQuestion(),
    history: []
}

export const questions = createSlice({
  name: 'questions',
  initialState: initialState,
  reducers: {
    trySolve: (state, answer: PayloadAction<number>) => {
        if (state.current) {
            state.current.submitted = answer.payload
            if (state.current.left * state.current.right == answer.payload) {
                state.current.state = QuestionState.RIGHT;
                state.score.right += 1
            } else {
                state.current.state = QuestionState.WRONG
                state.score.wrong += 1
            }
        }
    },
    nextQuestion: (state) => {
        if (state.current)
            state.history.unshift(state.current)
        state.current = newQuestion()
    },
  },
})

// Action creators are generated for each case reducer function
export const { trySolve, nextQuestion } = questions.actions

export default questions.reducer