import './App.css';
import { Container, Row, Col } from 'react-bootstrap';
import QuestionCard from './components/QuestionCard';
import QuestionHistory from './components/QuestionHistory';
import ScoreBoard from './components/ScoreBoard';
import { SubmissionType } from './model/Question';


function App() {
  const q = {left: 4, right: 12}
  const params = new URLSearchParams(window.location.search)
  const submissionType = (params.get("submissionType") ?? "SELECT") as SubmissionType

  return (
    <div className="App">
      <img src="/banner.png"/>
      <Container fluid>
        <Row style={{marginTop: "10px"}}>
          <Col xs={{order: 2}} md={{order: 1}}><ScoreBoard /></Col>
          <Col xs={{order: 1}} md={{order: 2}}><QuestionCard submissionType={submissionType} /></Col>
          <Col xs={{order: 3}} md={{order: 3}}><QuestionHistory /></Col>
        </Row>
      </Container>
    </div>
  );
}

export default App;
